let map = document.querySelector("#map")

function showMap() {
    let cords = $(".contacts-page .tab._active").data("cord")

    ymaps.ready(init);

    function init(){
        map.innerHTML = ''

        let myMap = new ymaps.Map("map", {
            center: cords,
            zoom: 16,
            controls: []
        });

        var myPlacemark = new ymaps.Placemark(cords, null, {
            preset: 'islands#redCircleDotIcon'
        });
        myMap.geoObjects.add(myPlacemark);
    }
}

if(map){
    showMap()
}



$(".contacts-page .tab").click(function () {
    $(".contacts-page .tab").removeClass("_active")
    $(this).addClass("_active")
    showMap()
    $(".contacts-page__nav .search-links__current").html($(".tab._active").html())
})

$(".contacts-page__nav .search-links__current").html($(".tab._active").html())

