$(".menu-item").hover(onIn, onOut);

$(".menu-item__show").click(function () {
    $(this).parents(".menu-item").toggleClass("_show")
    $(this).siblings(".menu-item__hidden").slideToggle()
})


function onIn() {
    if (window.innerWidth > 992) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("_show")
            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 992) {
        $(this).removeClass("_show")
    }
}


$(".burger").click(function () {
    $(".header").toggleClass("_open-menu")
    $("body").toggleClass("fixed-body")
})


$(window).scroll(function(){
    setFixHeader()
});
$(window).resize(function(){
    setFixHeader()
    document.documentElement.style.setProperty('--headerHeight', `${getHeaderHeight()}px`);
});

document.documentElement.style.setProperty('--headerHeight', `${getHeaderHeight()}px`);

setFixHeader()


function setFixHeader() {
    let headerHeight = getHeaderHeight()
    let st = $(this).scrollTop();
    if(st > headerHeight * 1.5){
        $(".header").addClass("_transform")
    } else{
        $(".header").removeClass("_transform")
    }
    if(st > headerHeight * 2){
        $(".header").addClass("_fix")
    } else{
        $(".header").removeClass("_fix")
    }
}

function getHeaderHeight() {
    let header = document.querySelector(".header")
    if(!header){
        return
    }
    return header.getBoundingClientRect().height
}
