$(".product-info__show").click(function () {
    $(this).parents(".product-info").toggleClass("_active")
    $(this).siblings(".product-info__hidden").slideToggle()
})

$(window).on('load resize', function(){
    if ($(window).width() <= 992) {
        $('.catalog-item2 .gallery').insertAfter('.catalog-item2__top');
    } else{
        $('.catalog-item2 .gallery').insertBefore('.may-like');
    }
});
