function mainSlider() {
    var swiper = new Swiper('.main-slider .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        pagination: {
            el: '.main-slider .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
    })
}

function Sliders() {
    let SlidersArray = []
    $(".slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            watchSlidesProgress: true,
            preventClicks :true,
            a11y: false,
            noSwipingSelector: "button",
            navigation: {
                nextEl: `.slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
        })
    })
}

function catalogCardSliders() {
    let SlidersArray = []
    $(".card-slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.card-slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            effect: 'fade',
            allowTouchMove: false,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            pagination: {
                el: `.card-slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
        })

        $(`.card-slider[data-slider-id="${index}"] .swiper-pagination-bullet`).hover(function () {
            $(this).click()
        })
    })
}

function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        spaceBetween: 0,
        slidesPerView: "auto",
        freeMode: true,
        watchSlidesProgress: true,
        direction: "vertical"
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 0,
        loop: true,
        thumbs: {
            swiper: swiper,
        },
        navigation: {
            nextEl: `.gallery .swiper-button-next`,
            prevEl: `.gallery .swiper-button-prev`,
        },
    });
}

mainSlider()
Sliders()
catalogCardSliders()
gallery()


$(".card-toggler__item").hover(function () {
    if(window.innerWidth>992){
        setColor(this)
    }
})
$(".card-toggler__item").click(function () {
    if(window.innerWidth<=992){
        setColor(this)
    }
})

function setColor(el) {
    let id = $(el).attr("data-slider")
    $(el).parents(".catalog-card").find(".card-toggler__item").removeClass("_active")
    $(el).addClass("_active")
    $(el).parents(".catalog-card").find(".catalog-card__view").removeClass("_active")
    $(el).parents(".catalog-card").find(`.catalog-card__view[data-slider="${id}"]`).addClass("_active")
}



$(".card-toggler").each(function (index,el) {
    let itemsCount = $(this).find(".card-toggler__item").length
    if(itemsCount>7){
        $(this).css("width","6.25em")
    }
})

